footer{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.list-1{
  display: flex;
  justify-content: space-evenly;
  width: 100vw;
  height: auto;
  padding: 10px 0px;
  background-color: rgb(255, 255, 255);
}
.list-2{
  width: 100vw;
  height: auto;
  display: flex;
  background-color: rgb(165, 242, 246);
  align-items: center;
  justify-content: center;
  border-top: 2px solid black;
}
.group{
  width: 20vw;
  height: auto;
  padding: 1%;
}
.one{
  margin-left: 20px ;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.four{
  margin-right: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.shaastralogo{
  width: 8vw;
  margin-bottom: 22px;
}
.qr{
  width: 8vw;
}
button{
  border-radius: 10px;
  padding: 5px;
  background: rgb(182, 254, 255);
  
}
.icons{
  width: 100%;
  font-weight: bolder;
  display: flex;
  justify-content: space-evenly;
}
.section-1{
  width: 45vw;
  display: flex;
  justify-content: space-between;
}
.section-2{
  width: 45vw;
  display: flex;
  justify-content: space-between;
}
@media screen and (max-width: 850px) {
  .list-1{
    flex-direction: column;
    padding: 10px 0px;
    justify-content: center;
    align-items: center;
    gap: 20px;
  }
  .section-1{
    width: 80vw;
    display: flex;
    justify-content: space-between;
  }
  .section-2{
    width: 80vw;
    display: flex;
    justify-content: space-between;
  }
  .group{
    width: 35vw;
    margin: 0%;
  }

}
@media screen and (max-width: 500px) {
  .list-1{
    flex-direction: column;
    padding: 0px ;
  }
  .group{
    width: 35vw;
    margin: 0%;
  }
  .shaastralogo{
    width: 25vw;
    margin-bottom: 20px;
  }
  .qr{
    width: 25vw;
  }
  .one{
    margin-left: 0px ;
  }
  .four{
    margin-right: 0px;
    margin-top: 10px rgb(255, 0, 0);
  }

}
@media screen and (max-width: 430px){
  .section-2{
    flex-direction: column;
  }
  .three{
    width: 80vw;
    text-align: center;
  }
  .four{
    width: 80vw;
  }
}